import React from "react";
import { Bar, BarChart, XAxis, YAxis } from "recharts";
// import VideoItem from "../video_item/video_item";
import styles from "./searchAnalysis.module.css";

const SearchAnalysis = ({ results, videos }) => {
  // console.log(results);
  // console.log(videos);
  const month = results.map((video) => {
    // console.log(video.viewCount)
    return ({view: video.viewCount, month: (new Date(video["year-month-date"]).getMonth())})
  }
  );
  // console.log(month)
  const data = [];
  const objdata = [];
  for (let i = 1; i < 13; i++) {
    let obj = {count:3};
    const temp = month.filter((m) => m.month == i);
    const length = temp.length;
    obj["name"] = i
    obj["count"]= length
    const avg = (temp.reduce((sum, curr) => sum+ curr.view, 0)/length/1000).toFixed(0)
    obj["avg"] = avg > 0? Number(avg) : 0
    data.push(length)
    objdata.push(obj);
  }

  
  // console.log(data);
  // console.log(objdata);

  const renderCustomBarLabel = ({ payload, x, y, width, height, value }) => {
    console.log(payload)
    return (
      <text
        x={x + width / 2}
        y={y}
        fill="#666"
        textAnchor="middle"
        dy={-6}
      >{`${value}`}</text>
    );
  };

  return (
    <>
      <p className={styles.meta}>{results.length}  .</p>
      <p className={styles.meta}>
          {((videos.length / results.length) * 100).toFixed(0)} % ,{" "}
        {videos.length}   10 .{" "}
      </p>
      <p className={styles.meta}>
         {" "}
        {(
          results.reduce((sum, current) => sum + current.viewCount, 0) / 1000
        ).toFixed(0)}{" "}
        k,  {" "}
        {(
          results.reduce((sum, current) => sum + current.viewCount, 0) /
          results.length /
          1000
        ).toFixed(0)}{" "}
        k
      </p>
      {/* {data.map((count, index) => (
        <span>
          {" "}{count}{" "}
        </span>
      ))} */}
      <h3>  / </h3>
      <BarChart width={600} height={300} data={objdata}>
        <XAxis dataKey="name" />
        <YAxis yAxisId="left" orientation="left" stroke="#8884d8" />
        <YAxis yAxisId="right" orientation="right" stroke="#82ca9d" />
        <Bar
          yAxisId="left"
          dataKey="count"
          barSize={30}
          fill="#8884d8"
          label={renderCustomBarLabel}
        />
        <Bar
          yAxisId="right"
          dataKey="avg"
          barSize={30}
          fill="#82ca9d"
          label={renderCustomBarLabel}
        />
        {/* <Bar dataKey="count" barSize={30} fill="#000000" /> */}
      </BarChart>
    </>
    // <ul className={styles.videos}>
    //   {props.videos.map((video) => (
    //     <VideoItem key={video.videoId} video={video} />
    //   ))}
    // </ul>
  );
};

export default SearchAnalysis;
